import { Component, OnInit } from '@angular/core';
import { trigger, transition, style, animate, query, stagger, state } from '@angular/animations';
import { Fakedata } from './../fakedata/fakedata';
const listAnimation = trigger('listAnimation', [
  transition('* <=> *', [
    query(':enter',
      [style({ opacity: 0 }), stagger('200ms', animate('600ms ease-out', style({ opacity: 1 })))],
      { optional: true }
    ),
    query(':leave',
      animate('200ms', style({ opacity: 0 })),
      { optional: true }
    )
  ])
]);
const flyInList = trigger('flyInList', [
  transition('* <=> *', [
    query(':enter',
      [
        style({ opacity: 0, transform: 'translateY(100%)', }), stagger('200ms', animate('600ms 4s ease-out',
          style({
            opacity: 1,
            transform: 'translateY(0%)'
          })
        )
        )],
      { optional: true }
    )
  ])
])
const flyIn1s = trigger('flyIn1s', [
  state('void', style({
    transform: 'translateX(150%)',
    opacity: 1
  })),
  transition('void <=> *', animate('2s 500ms ease-in')),
]);
const fadeIn1s = trigger('fadeIn1s', [
  state('void', style({
    opacity: 0
  })),
  transition('void <=> *', animate('4s 2000ms ease-in')),
]);
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  animations: [listAnimation, flyIn1s, flyInList, fadeIn1s]
})
export class HomeComponent implements OnInit {
  public slides = Fakedata.slides;
  public slogan = Fakedata.slogan;
  public tuyendung= Fakedata.tuyendung;
  public doitacs = Fakedata.doitacs;
  slideConfig = {
    // draggable: true,
    // centerMode: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 6000,
    dots: true,
    infinite: true,
    arrows: false,
    // responsive: [
    //   { breakpoint: 2000, settings: { slidesToShow: 4, slidesToScroll: 4, } },
    //   // { breakpoint: 1600, settings: { slidesToShow: 3, slidesToScroll: 3, } },
    //   { breakpoint: 1400, settings: { slidesToShow: 3, slidesToScroll: 3, } },
    //   // { breakpoint: 1200, settings: { slidesToShow: 2, slidesToScroll: 2, } },
    //   // { breakpoint: 1000, settings: { slidesToShow: 2, slidesToScroll: 2, } },
    //   { breakpoint: 800, settings: { slidesToShow: 2, slidesToScroll: 2, } },
    //   { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, } }]
  };
  constructor() { }

  ngOnInit() {
  }
  link(str){
    if(str!=undefined&& str.trim()!==''){
      window.open(str).focus();
    }
  }
}
