import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Fakedata } from '../fakedata/fakedata';

@Component({
  selector: 'app-recruititem',
  templateUrl: './recruititem.component.html',
  styleUrls: ['./recruititem.component.css']
})
export class RecruititemComponent implements OnInit {
  public item: any = {};
  public recId = parseInt(this.activeroute.snapshot.paramMap.get('id'), 0);

  constructor(private activeroute: ActivatedRoute, ) {
    this.item = Fakedata.tuyendung.filter((ele: any) => ele.id === this.recId )[0];
  }

  ngOnInit() {
    console.log(this.item);
    console.log(this.recId);
  }

}
